.App {
  text-align: center;
}

body {
  background-color: lightyellow
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.a2:hover {
  background-color: red;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

#footer {
  background-color: black;
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;
}

li {
  float: left;
}

li a {
  display: block;
  color: white;
  font-size: 20px;
  text-align: center;
  padding: 30px 30.3px;
  text-decoration: none;
}

li a:hover {
  background-color: #111;
}

.searchcard {

  background-color: skyblue;
  color: #fff;

  min-height: 80px;
  position: sticky;
  top: 0;

}

.newspage {
  padding: 10px;
  margin-left: 80px;
  margin: 10px;
  width: 300px;
  height: 550px;
  grid-template-columns: auto auto auto;
  background-color: rgb(161, 228, 161);
  transition: transform 0.3s ease;
  transform-origin: center center;
  box-shadow: 2px 5px 10px black;
}

.email {
  padding: 10px;
  width: 100%;
  height: 900px;
  display: grid;
  grid-template-columns: auto;
  background-color: rgb(161, 228, 161);
  box-shadow: 10px 15px 20px black;
  margin-bottom: 200px;
}

#btn:hover {
  background-color: red;
  cursor: pointer;
}

body {
  height: 100%;
  width: 100%;
}

.newscard {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  gap: 80px;
  margin-bottom: 1500px;
}

.img {
  height: 200px;
  width: 300px;
  background-image: url("data:image/jpeg;base64,");
}

h3 {
  color: red;
  background-color: rgb(234, 234, 148);
}

h4 {
  background-color: azure;
  border-radius: 25px;
}

button {
  border-radius: 25px;
  cursor: pointer;
  padding-left: 50px;
}

#count {
  width: 80px;
  height: 30px;
  border-radius: 25px;
  padding-left: 10px;
}

button:hover {
  background-color: red;
}

h2 {
  background-color: #91cada;
}

h2,
h3,
h4,
button {
  padding: 10px;
  margin-left: 10px;
  margin: 10px;
  width: fit-content;
  grid-template-columns: auto auto auto;
  transition: transform 0.3s ease;
  transform-origin: center center;
  box-shadow: 2px 5px 10px black;
}

h3:hover {
  transform: scale(1.1);
}

h2:hover {
  transform: scale(1.1);
}

h4:hover {
  transform: scale(1.1);
}

h1 {
  align-items: left;
}

#msg {
  font-size: 30px;
}

.search {
  display: flex;
  gap: 750px;
  background-color: #61dafb;
  margin-bottom: 50px;
  padding: 10px;
  box-shadow: 5px 10px 15px rgb(10, 9, 9);
  border-radius: 25px;

}

.cart {

  font-size: 20px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  height: 50px;
  width: 300px;
  margin: 10px;
  padding-right: 500px;
  padding-left: 20px;
  border-radius: 25px;

}

.cart1{

  font-size: 20px;
