  margin: 10px;
  padding: 20px;
}

.json {
  background-color: aqua;
}

.price {
  display: flex;
}

.searchcard {
  background-color: skyblue;
  margin-bottom: 100px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 500px;
}

#homepage {
  padding-bottom: 500px;
}

#home {
  height: 600px;
  width: 1200px;
}


@media (max-width: 450px) {
  .newspage {
    padding: 10px;
    margin-left: 80px;
    margin: 10px;
    width: 300px;
    justify-content: center;
    height: 420px;
